import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Block } from 'jsxstyle'
import Loading from './Loading'
import GaugeR from './GaugeR'


class RecentImages extends Component {
  state = {
    recents: []
  }

  componentDidMount() {
    this.preload()
  }

  preload() {
    const { howMany } = this.props;
    var that = this;
    fetch('http://46.45.187.173/LikeometerTest/page/evaluations', {})
      .then(function (response) {
        return response.json();
        //console.log('request succeeded with JSON response', data)
      }).then(function (data) {
        that.setState({ recents: data.slice(0, howMany) });
      }).catch(function (error) {
        console.log('request failed', error)
      });

  }

  render() {
    const { recents } = this.state
    return (
      <div className="row">
        {recents ? (
          recents.map((item, i) => (
            <Box key={i} url={item.Url} rank={item.Result.Rank * 100} strength={item.Result.Score + ""} interactionCount={item.Result.InteractionCount} />
          ))
        ) : (
            <Loading />
          )}
      </div>
    )
  }
}

RecentImages.propTypes = {
  howMany: PropTypes.number
}

const Box = ({ url, rank, strength, fanBase, interactionCount }) => (
  <div className="col-sm">
    <div className="box with-radius with-shadow">
      <img src={url} />
      <div className="box-info row">
        <div className="col-4">
          <GaugeR value={rank} />
        </div>
        <div className="col-8 box-info-details">
          <span>Strength <b>{strength}</b></span>
          <span>Fan Base <b>{fanBase}</b></span>
          <span>Estimated Likes <b>{interactionCount}</b></span>
        </div>
      </div>
    </div>
  </div>
)

Box.propTypes = {
  url: PropTypes.string,
  strength: PropTypes.string,
  rank: PropTypes.number,
  fanBase: PropTypes.number,
  interactionCount: PropTypes.number
}

export default RecentImages




