import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'

class Facebook extends Component {
  state = {
    user: null,
    pages: null,
    working: false
  }

  componentDidMount() {
    this.status()
  }

  status = () => {
    console.log("Fb Status")
    FB.getLoginStatus(function (response) {
      console.log(response);
      if (!response || response.error) {

      } else {

      }
    });
  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  login = () => {
    var that = this;
    console.log("works?")
    this.busy();
    FB.login(function (loginResponse) {
      console.log(loginResponse);
      //accessToken,expiresIn,signedRequest,userID

      if (loginResponse.authResponse) {
        that.setState({ fbAuth: loginResponse.authResponse });
        that.extendToken(loginResponse.authResponse.accessToken);
      }
      //that.busy(false);
    },
      {
        scope: 'public_profile,email,user_friends,manage_pages,publish_pages,pages_messaging,pages_messaging_phone_number,pages_messaging_subscriptions,read_insights'
      });
  }

  extendToken = (token) => {
    var that = this;
    fetch('http://onedestapidev.azurewebsites.net/configuration/extendAccessToken?appId=749679035174071&accessToken=' + token, {})
      .then(function (response) {
        return response.json();
      }).then(function (data) {
        that.setState({ extendedToken: data });
        that.pages(data);
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }
  signup = (name, token, pageId, fanCount) => {
    let data = {
      "Name": "string",
      "PageId": pageId,
      "PageToken": "string",
      "PageType": 0,
      "FanCount": fanCount
    };
    fetch('http://46.45.187.173/LikeometerTest/user/organization', {
      method: "POST",
      body: JSON.stringify(data)
    })
      .then(function (data) {
        console.log('request succeeded with JSON response', data)
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }

  logout = () => {
    FB.logout(function (response) { });
  }

  pages = (longToken) => {
    var that = this;
    FB.api('me/accounts', 'get', { access_token: longToken, fields: 'picture,fan_count,name,category,access_token' }, function (accountsResponse) {
      if (accountsResponse.data) {
        that.setState({ pages: accountsResponse.data });
        console.log(that.state.pages);
      }
    });
  }

  render() {
    const { pages, working } = this.state;
    return (
      <div style={{ textAlign: "center" }} >
        <button className="btn btn-primary btn-radius fb-connect" onClick={this.login} >
          <i className="fa fa-facebook"></i>&nbsp;&nbsp;&nbsp;&nbsp;CONNECT WITH FACEBOOK</button> <br />
        <span>to learn your number of likes</span>
        {working && (<Spinner />)}
        {pages && (
          <div className="dropdown-menu" style={{ position: "static", display: "block" }}>
            <a className="dropdown-item" href="#">Select Page</a>
            <div className="dropdown-divider"></div>
            {pages.map((item, i) => (
              <a className="dropdown-item" href="#" key={i}>
                <img src={item.picture.data.url} />&nbsp;&nbsp;
                {item.name} <small>, {item.fan_count}</small></a>
            ))}
          </div>
        )}
      </div>
    )
  }
}
export default Facebook



