import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Block } from 'jsxstyle'
import { DARK_GRAY } from '../../Theme'
import Facebook from '../Facebook'


class Analyze extends Component {

  state = {
    files: null
  }

  drawww = () => {
    var opts = {
      angle: 0,
      lineWidth: 0.20,
      radiusScale: 1,
      pointer: {
        length: 0.34,
        strokeWidth: 0.031,
        color: '#000000'
      },
      limitMax: true,
      limitMin: true,
      generateGradient: true,
      highDpiSupport: true,
      staticZones: [
        { strokeStyle: "#b1242c", min: 0, max: 100 },
        { strokeStyle: "#e74435", min: 100, max: 200 },
        { strokeStyle: "#f5572a", min: 200, max: 300 },
        { strokeStyle: "#f49227", min: 300, max: 400 },
        { strokeStyle: "#f0b245", min: 400, max: 500 },
        { strokeStyle: "#faea33", min: 500, max: 600 },
        { strokeStyle: "#d7db2d", min: 600, max: 700 },
        { strokeStyle: "#89ca4a", min: 700, max: 800 },
        { strokeStyle: "#33b448", min: 800, max: 900 },
        { strokeStyle: "#108a49", min: 900, max: 1000 }
      ],
      animationSpeed: 15
    };
    var target = document.getElementById('gauge');
    var gauge = new Gauge(target).setOptions(opts);
    gauge.maxValue = 1000;
    gauge.setMinValue(0);

    gauge.set(300);
  }

  openFileDialog = (e) => {
    var form = document.getElementById('file');
    form.click();
  }

  handleClick = (e) => {
    var data = new FormData();
    for (var i = 0; i < this.state.files.length; i++) {
      data.append("file-" + i, this.state.files[i]);
    }

    fetch('http://46.45.187.173/LikeometerTest/page/sortimagefiles', {
      method: 'POST',
      body: data
    })
      .then(function (data) {
        console.log('request succeeded with JSON response', data)
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }

  componentDidMount() {
    console.log("componentDidMount")
    this.drawww();
  }

  handleChange = (e) => {
    var fls = [];
    for (var i = 0; i < e.target.files.length; i++) {
      let f = e.target.files[i];
      f.key = e.target.files[i].name;
      fls.push(f);
      //console.log(this.state.files[i]);
    }
    console.log(fls);
    this.setState({ files: fls });
  }

  render() {
    const { files } = this.state
    return (
      <div className="container">
        <div className="row">
          <div className="col-sm">
            <div className="box with-border">
              <div className="img-detail row align-items-center">
                <div className="col-2">
                  <i className="fa fa-user-circle-o" style={{ fontSize: 35, color: "#335698", border: "1px solid #ccc", padding: "5px 5px" }}></i></div>
                <div className="col-10" style={{ textAlign: "left" }}>
                  <a href="#">Turkey.Home</a><br />
                  <small>0 mins</small>
                </div>
              </div>
              <div className="img-detail row align-items-center">
                <div className="col">
                  <img src="photo@3x.png" />
                </div>
              </div>
              <div className="img-results row align-items-center">
                <div className="col-6" ><small>0 likes</small>&nbsp;&nbsp; <small>&bull; 0 Comments</small></div>
                <div className="col-6">
                  --
            </div>
              </div>
            </div>
          </div>
          <div className="col-sm">
            <div className="box with-border analyze-results">
              <h3>Strength</h3>
              <div className="box-results row">
                <div className="col-4" style={{ margin: 0 }}>
                  <canvas width="135" height="70" id="gauge"></canvas>
                </div>
                <div className="col-8">
                  Your image is rated as <br />
                  <b style={{ fontSize: "130%" }}>8</b>/10
            </div>
              </div>
              <div className="line"></div>
              <h3>Estimated Results </h3>
              <div className="box-results row">
                <div className="col">
                  <b>Fan base</b> <br /> 4.894.892<br /><br />
                  <b>Likes</b> <br /> 4.824 - 5.250 (Out of 8.250)<br />
                  <div style={{ marginBottom: "25px" }} className="progress">&nbsp;<div className="progress-inc" ></div>
                  </div>

                  <Facebook >
                  </Facebook>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Analyze



