import React from 'react'
import { Link } from 'react-router-dom'
import { Block, Flex, Row, Inline } from 'jsxstyle'
import { SMALL_SCREEN, LIGHT_GRAY, BRIGHT_GRAY } from '../../Theme'
import Logo from '../Logo'
import SmallScreen from '../SmallScreen'

const NavLink = ({ href, ...props }) => (
  <Block
    component="a"
    props={{ href }}
    margin="0 10px"
    {...props}
  />
)

const Button = ({ to, small, ...props }) => (
  <Block component={Link}
    activeBoxShadow="2px 2px 4px rgba(0,0,0,.25)"
    activeTop="5px"
    background="white"
    borderRadius="100px"
    boxShadow={small ? (
      '0 5px 15px rgba(0, 0, 0, .25)'
    ) : (
        '0 10px 30px rgba(0, 0, 0, .25)'
      )}
    cursor="pointer"
    flex="1"
    fontSize="10px"
    fontWeight="bold"
    hoverBoxShadow={small ? (
      '0 5px 10px rgba(0, 0, 0, .25)'
    ) : (
        '0 10px 25px rgba(0, 0, 0, .25)'
      )}
    hoverTop="1px"
    marginRight={small ? '10px' : '20px'}
    padding={small ? '10px' : '15px 25px'}
    position="relative"
    props={{ to }}
    textAlign="center"
    textTransform="uppercase"
    top="0"
    userSelect="none"
    whiteSpace="nowrap"
    {...props}
  />
)

const NavBar = () => (
  <Row
    textTransform="uppercase"
    fontWeight="bold"
    width="100%"
  >
    <Block flex="1" fontSize="14px">
      <Inline component="a" props={{ href: "https://reacttraining.com" }}>
        React Training
      </Inline>
      <Inline> / </Inline>
      <Inline
        component="a"
        props={{ href: 'https://github.com/ReactTraining/react-router' }}
        color={LIGHT_GRAY}
      >React Router</Inline>
    </Block>
    <Row fontSize="12px">
      <NavLink href="https://github.com/ReactTraining/react-router">Github</NavLink>
      <NavLink href="https://www.npmjs.com/package/react-router">NPM</NavLink>
      <NavLink href="https://reacttraining.com" margin="0">Get Training</NavLink>
    </Row>
  </Row>
)

const Banner = () => (
  <SmallScreen>
    {(isSmallScreen) => (
      <Row width="100%">
        {!isSmallScreen && (
          <Block flex="1">
            <Logo />
          </Block>
        )}
        <Block flex="1">
          <Block lineHeight="1">
            <Block
              textTransform="uppercase"
              fontSize={isSmallScreen ? '80%' : '120%'}
              fontWeight="bold"
            >
              Learn once, Route anywhere
            </Block>
            <Block
              component="h2"
              textTransform="uppercase"
              fontSize={isSmallScreen ? '200%' : '350%'}
              fontWeight="bold"
            >
              React Router
            </Block>
          </Block>

          <Block
            margin={`${isSmallScreen ? 20 : 20}px 0`}
            fontSize={isSmallScreen ? '80%' : null}
          >
            Components are the heart of React's powerful, declarative
            programming model. React Router is a collection of <b>navigational
            components</b> that compose declaratively with your application. Whether
            you want to have <b>bookmarkable URLs</b> for your web app or a composable
            way to navigate in <b>React Native</b>, React Router works wherever React
            is rendering--so take your pick!
          </Block>

          <Row>
            <Button to="/web" small={isSmallScreen}>Web</Button>
            <Button to="/native" small={isSmallScreen}>Native</Button>
            <Button to="/core" small={isSmallScreen}>Anywhere</Button>
          </Row>
        </Block>
      </Row>
    )}
  </SmallScreen>
)

const Header = () => (
  <header className="navbar navbar-default navbar-toggleable-md bd-navbar">
    <nav className="container">
      <div className="d-flex justify-content-between hidden-lg-up">
        <a className="navbar-brand" href="/">
          <img src="/logo.png" />
        </a>
        <button className="navbar-toggler" type="button">
          <span className="navbar-toggler-icon"></span>
        </button>
      </div>

      <div className="collapse navbar-collapse" id="bd-main-nav">
        <ul className="nav navbar-nav">
          <li className="nav-item active">
            <a className="nav-item nav-link " href="/"><img src="/logo.png" /></a>
          </li>
        </ul>
      </div>
    </nav>
  </header>
)

export default Header



